/*
 *  Logo
 *    Styles for the logos section.
 *
 *  Version:
 *    1.0.0 - 2021/09/21
 */

.c-logos {
  background-color: $s-color--background-subdued;
  padding-top: 3.75rem;

  &__title {
    padding: 0 $s-gutter--edge-mobile;
    margin-bottom: 1.9375rem;
  }

  &__container {
    padding-left: $s-gutter--edge-mobile;
    overflow-x: scroll;
    overflow-y: hidden;
    cursor: grab;
    user-select: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    &.is-active {
      cursor: grabbing;
    }
  }

  &__overflow {
    white-space: nowrap;
    user-select: none;
  }

  &__logo {
    width: auto;
    height: auto;
    max-height: 1.9375rem;
    user-select: none;

    &-item {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      margin-right: 2rem;
      user-select: none;

      &:last-child {
        margin-right: $s-gutter--edge-mobile;
      }
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    padding: 3.75rem 0 0 $s-gutter--edge-mobile;

    &__wrapper {
      display: flex;
      align-items: center;
    }

    &__title {
      padding: 0;
      margin-right: 2rem;
      margin-bottom: 0;
      white-space: nowrap;
    }

    &__container {
      flex-grow: 1;
      padding-left: 0;
    }

    &__logo {
      max-height: 2.375rem;

      &-item {
        margin-right: 2.75rem;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 5rem #{'min(13%, 10.125rem)'} 0;

    &__title {
      margin-right: 2.5rem;
    }

    &__wrapper {
      position: relative;

      &::after {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 2rem;
        background: linear-gradient(to right, rgba($s-color--background-subdued, 0), rgba($s-color--background-subdued, 1));
        height: 100%;
        z-index: $s-z--text-overlay;
        opacity: 0;
        pointer-events: none;
        content: "";
      }

      &.is-overflowing::after {
        opacity: 1;
      }
    }

    &__logo {
      &-item {
        margin-right: 3.75rem;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 6.375rem #{'min(13%, 16.875rem)'} 0;

    &__inner {
      max-width: 91.25rem;
      margin: 0 auto;
    }

    &__title {
      margin-right: 4.0625rem;
    }

    &__logo {
      max-height: 2.875rem;

      &-item {
        margin-right: 6rem;
      }
    }
  }
}
